    delete (collection, id) {
      return new Promise(function (resolve, reject) {
        // const idb = window.indexedDB
        let open = idb.open('gtvappdb', 1)
        open.onsuccess = () => {
          let db = open.result
          let tx = db.transaction(collection, 'readwrite')
          let store = tx.objectStore(collection)
          // let catIndex = catStore.index('NameIndex')
          let req = store.delete(id)
          req.onsuccess = () => { resolve(req.result) }
          req.onerror = function (error) { reject(error) }
          tx.oncomplete = () => { db.close() }
        }
        open.onerror = function (error) { console.log(error); reject(error) }
      })
    },
    deleteSQL (collection, id) {
      // console.log(JSON.stringify({fn:'deleteSQL', collection, id}))
      return new Promise(function (resolve, reject) {
        let reqRes = []
        // let idk = collection === 'Pdf' ? 'code_pj' : '_id'
        sqldb.transaction(
          (tx) => {
            tx.executeSql(
              `delete from ${collection} where _id=?`,
              [id]
            )
          },
          (err) => { reject(err) },
          () => { /* console.log('SQL DELETE SUCCESS') */; resolve() }
        )
      })
    },
    put (collection, data) {
      return new Promise(function (resolve, reject) {
        // const idb = window.indexedDB
        let open = idb.open('gtvappdb', 1)
        open.onsuccess = () => {
          let db = open.result
          let tx = db.transaction(collection, 'readwrite')
          let store = tx.objectStore(collection)
          // let catIndex = catStore.index('NameIndex')
          let req = store.put(data)
          req.onsuccess = () => { resolve(req.result) }
          req.onerror = function (error) { reject(error) }
          tx.oncomplete = () => { db.close() }
        }
        open.onerror = function (error) { console.log(error); reject(error) }
      })
    },
    putSQL (collection, elem) {
      // console.log(JSON.stringify({fn:'putSQL', collection, elem}))
      return new Promise(function (resolve, reject) {
        // let idk = collection === 'Pdf' ? 'code_pj' : '_id'
        let nwId = elem['_id']
        delete elem['_id']
        let nwData = JSON.stringify(elem)
        let reqRes = []
        sqldb.transaction(
          (tx) => {
            tx.executeSql(
              `insert into ${collection} (_id, data) values (?,?)`,
              [nwId.toString(), nwData]
            )
          },
          (err) => { reject(err) },
          () => { /* console.log('SQL PUT SUCCESS') */; resolve() }
        )
      })
    },
