export default {
  name: 'Docset',
  data () {
    return {
      docsetId: this.$route.params.id,
      docsets: window.gtvapp.docsets,
      docSet: [],
      md: new window.utils.md(),
      db: window.gtvapp.db
    }
  },
  methods: {
    // getDoc (id) {
    //   console.log(`${window.gtvapp.interfaceUrl}/PDF/${id}/${window.gtvapp.id_mobile}`)
    // }
    b64toBlob (b64Data, contentType, sliceSize) {
      contentType = contentType || ''
      sliceSize = sliceSize || 512
      let byteCharacters = atob(b64Data.replace(/\s/g, ''))
      let byteArrays = []
      for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
        let slice = byteCharacters.slice(offset, offset + sliceSize)
        let byteNumbers = new Array(slice.length)
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i)
        }
        let byteArray = new Uint8Array(byteNumbers)
        byteArrays.push(byteArray)
      }
      var blob = new Blob(byteArrays, {type: contentType})
      return blob
    },
    savePdf (name, content) {
      let blob = this.b64toBlob(content,'application/pdf')
      return new Promise ((resolve, reject) => {
        if ((typeof cordova) !== 'undefined') {
          let contentType = 'application/pdf'
          let folderpath = device.platform === 'Android' ? cordova.file.externalDataDirectory : cordova.file.syncedDataDirectory
          console.log('cordova available, lets write the file system')
          console.log(folderpath)
          let filename = `${name}.pdf`
          window.resolveLocalFileSystemURL(folderpath, function (dir) {
            console.log('Access to the directory granted succesfully')
            dir.getFile(filename, { create: true }, function (file) {
              console.log('File created succesfully.')
              file.createWriter(function (fileWriter) {
                console.log('Writing content to file')
                fileWriter.write(blob)
                resolve(`${folderpath}${name}.pdf`)
              }, function () {
                console.log('Unable to save file in path '+ folderpath)
                reject()
              })
            })
          })
        } else {
          // cordova unavailable
          resolve()
        }
      })
    },
    async removePdf (code_pj) {
      // let pdf = await this.db.get('Pdf', code_pj)
      try {
        let path = device.platform === 'Android' ? cordova.file.externalDataDirectory : cordova.file.syncedDataDirectory;
        window.resolveLocalFileSystemURL(path, function(dir) {
        	dir.getFile(`${code_pj}.pdf`, {create:false}, function(fileEntry) {
            fileEntry.remove(function(){
                console.log(`${code_pj}.pdf has been removed succesfully`)
            },function(error){
                console.log(`${code_pj}.pdf : error during deleting`)
                console.log(error)
            },function(){
              console.log(`${code_pj}.pdf does not exist`)
              // console.log(error)
            })
        	})
        })
      } catch (e) {
        console.log(JSON.stringify(e));
      }
    },
    async openPdf (code_pj) {
      let pdf = await this.db.getSQL('Pdf', code_pj)
      // let pdf = await this.db.get('Pdf', code_pj)
      // var folderpath = device.platform === 'Android' ? cordova.file.externalDataDirectory : cordova.file.syncedDataDirectory;
      // var path = folderpath+'test.txt';
      try {
        var platform = device.platform;
        if (pdf.filepath && pdf.filepath.length > 0){
          var ref = window.open(
            decodeURI(pdf.filepath),
            platform === 'Android' ? '_system' : '_blank',
            'location=yes'
          );
        }
      } catch (e) {
        console.log(JSON.stringify(e));
      }
    },
    async synchronize (pj) {
      // const idb = window.indexedDB
      // let open = idb.open('gtvappdb', 1)
      let pdf = await this.db.getSQL('Pdf', pj.code_pj)
      // let pdf = await this.db.get('Pdf', pj.code_pj)
      if(pdf === undefined){
        // await this.db.put('DocSets', d)
        console.log('unknown')
        window.gtvapp.wait.c++
        this.$http.get(
          `${window.gtvapp.interfaceUrl}/Fichier/${pj.code_pj}/${window.gtvapp.id_mobile}`/*,
          {headers:window.gtvapp.interfaceOptions}*/
        ).then(
          async res => {
            let filepath = await this.savePdf(pj.code_pj, res.body.data.fichier_xml)
            await this.db.putSQL('Pdf', {
              _id: pj.code_pj,
              title: pj.title,
              // content: res.body.data.fichier_xml,
              filepath: filepath,
              synchronized: true
            })
            // await this.db.put('Pdf', {
            //   _id: pj.code_pj,
            //   title: pj.title,
            //   content: res.body.data.fichier_xml,
            //   filepath: filepath,
            //   synchronized: true
            // })
            pj.synchronized = true
            console.log('pdf synchronized')
            this.$forceUpdate()
            window.gtvapp.wait.c--
          }
        )
      } else {
        console.log('pdf already synchronized')
        pj.synchronized = true
        this.$forceUpdate()
      }
    },
    cmpKws (kws) {
      let cmpKws = (
        kws !== undefined && kws !== '' 
        ? kws.split(',').map((k) => {return k.trim().toLowerCase()}).join(' ').split(' ').map((k) => {return k.trim().toLowerCase()})
        : []
      )
      return cmpKws
    },
    async unsynchronize (pj) {
      console.log(JSON.stringify(pj))
      window.gtvapp.wait.c++
      this.removePdf(pj.code_pj)
      await this.db.deleteSQL('Pdf', pj.code_pj)
      // await this.db.delete('Pdf', pj.code_pj)
      pj.synchronized = false
      console.log('pdf unsynchronized')
      this.$forceUpdate()
      window.gtvapp.wait.c--
    },
    previous () {
      this.$router.go(-1)
    }
  },
  async mounted () {
    // console.log('DocSet update !!')
    let id = this.$route.params.id
    if(id !== undefined){
      // this.docSet = this._.find(this.docsets, function(o){return o._id == id})
      this.docSet = await this.db.getSQL('DocSets', id)
      // this.docSet = await this.db.get('DocSets', id)
      console.log(JSON.stringify(this.docSet))
      // this.docSet.cmpKws = (
      //   this.docSet.keywords !== undefined && this.docSet.keywords !== '' 
      //   ? this.docSet.keywords.split(',').map((k) => {return k.trim().toLowerCase()})
      //   : []
      // )
      this.docSet.docs.forEach( async (pj) => {
        // const idb = window.indexedDB
        // let open = idb.open('gtvappdb', 1)
        let pdf = await this.db.getSQL('Pdf', pj.code_pj)
        // let pdf = await this.db.get('Pdf', pj.code_pj)
        if (pdf !== undefined) {
          pj.synchronized = true
          this.$forceUpdate()
        }
      })
    } else {
      this.docset = []
    }
  },
  updated () {
  }
}


