export default {
  name: 'Home',
  data () {
    return {
      db: window.gtvapp.db,
      links: window.gtvapp.links
    }
  },
  methods: {
    getLinks () {
      this.$http.get(
        `${window.gtvapp.interfaceUrl}/Links/${window.gtvapp.id_mobile}`).then(
        res => {
          console.log('getLinks')
          window.gtvapp.links = res.body.Liens.liste
          this.links = window.gtvapp.links
          console.log(JSON.stringify(this.links))
          this.$forceUpdate()
        },
        err => {
          console.log(JSON.stringify(err))
          window.gtvapp.messages.push({ type: 'danger', message: `${err.body.code} : ${err.body.message}` })
        }
      )
    },
    checkForUpdate () {
      // INIT DB IF NEEDED
      // const idb = window.indexedDB
      // console.log('opendb')
      // let open = idb.open('gtvappdb', 1)
      // open.onupgradeneeded = () => {
      //   console.log('onupgradeneeded')
      //   let db = open.result
      //   let store = db.createObjectStore('Categories', {keyPath: '_id'})
      //   store.createIndex('NameIndex', ['name'])
      //   store = db.createObjectStore('DocSets', {keyPath: '_id'})
      //   store.createIndex('TitleIndex', ['title'])
      //   store = db.createObjectStore('Pdf', {keyPath: '_id'})
      //   store.createIndex('TitlePdfIndex', ['title'])
      // }
      // SQLITE INIT
      const sqldb = window.openDatabase("gtvappdb", "1.0", "GTVAPP database", 1000000); //will create database Dummy_DB or open it
      sqldb.transaction(
        (tx) => {
          tx.executeSql('CREATE TABLE IF NOT EXISTS Categories (_id INT PRIMARY KEY,data TEXT NOT NULL)')
          tx.executeSql('CREATE TABLE IF NOT EXISTS DocSets (_id TEXT PRIMARY KEY,data TEXT NOT NULL)')
          tx.executeSql('CREATE TABLE IF NOT EXISTS Pdf (_id TEXT PRIMARY KEY,data TEXT NOT NULL)')
        },
        (err) => { console.log('SQL ERROR : ' + err.code) },
        () => { console.log('SQL SUCCESS') }
      )
      // GET LAST NUM_EXPORT REQUESTED
      let lastNumExport = this.$ls.get('num_export')
      console.log('lastNumExport : ' + lastNumExport)
      if(lastNumExport === undefined || lastNumExport === null) lastNumExport = 0
      console.log('then next numExport wil be : ' + lastNumExport)
      // =========================================================================
      // Try to get datas (categories & docsets)
      window.gtvapp.wait.c++
      window.gtvapp.wait.msg = 'Chargement du contenu...'
      console.log('GET : Chargement du contenu...')
      console.log('Categories : ' + (window.gtvapp.categories !== undefined ? window.gtvapp.categories.length : '0'))
      console.log('Docsets : ' + (window.gtvapp.docsets !== undefined ? window.gtvapp.docsets.length : '0'))
      if (
        window.gtvapp.categories === undefined ||
        window.gtvapp.categories.length == 0 ||
        window.gtvapp.docsets === undefined ||
        window.gtvapp.docsets.length == 0
      ) {
        console.log('first insert')
        console.log(`GET ${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/${lastNumExport}`)
        this.$http.get(
          // `${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/189800`/*,
          `${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/${lastNumExport}`/*,
          {headers:window.gtvapp.interfaceOptions}*/
        ).then(
          async res => {
            // Connected : Get the last data according to num_export
            let categories = res.body.data.json.categories.data
            let docsets = res.body.data.json.docsets.data
            // =====================================================================
            // Update / Create categories in localDB
            console.log('put categories in db')
            window.gtvapp.wait.msg = 'Synchronisation des categories...'
            let catSyncPromises = []
            // ==
            categories.forEach(async (c) => {
              catSyncPromises.push(
                this.db.getSQL('Categories', c._id)
                .then(async (cat) => {
                  if (cat === undefined) {
                    console.log('Adding cat : ' + c._id)
                    await this.db.putSQL('Categories', c)
                  } else {
                    console.log('Updating cat : ' + cat._id)
                    if (c.docsets !== undefined) {
                      let nwDocsets = (cat.docsets.concat(c.docsets)).filter((value, index, self) => { 
                        return self.indexOf(value) === index
                      })
                      c.docsets = nwDocsets
                      await this.db.updateSQL('Categories', c, cat)
                    }
                  }
                })
                .catch((err) => {
                  console.log(JSON.stringify({c, err}))
                })
              )
            })
            // ==
            // GetAll Categories
            try {
              console.log('putting categories ...')
              await Promise.all(catSyncPromises)
              console.log('categories are in db')
              console.log('get Categories')
              window.gtvapp.categories = await this.db.getSQL('Categories')
              console.log('got Categories : ' + window.gtvapp.categories.length)
            } catch (ex) { console.log(JSON.stringify(ex)) }
            // =====================================================================
            // Update / Create docsets in localDB
            console.log('put docsets in db')
            window.gtvapp.wait.msg = 'Synchronisation des documents...'
            let docSyncPromises = []
            docsets.forEach(async (d) => {
              lastNumExport = Math.max(lastNumExport, d.num_export)
              docSyncPromises.push(
                this.db.getSQL('DocSets', d._id)
                .then(async (doc) => {
                  if (doc === undefined) {
                    console.log('Adding doc : ' + d._id)
                    await this.db.putSQL('DocSets', d)
                  } else {
                    console.log('Updating doc : ' + doc._id)
                    await this.db.updateSQL('DocSets', d, doc)
                  }
                })
                .catch((err) => {
                  console.log(JSON.stringify({d, err}))
                })
              )
            })
            console.log('new num export : ' + lastNumExport)
            this.$ls.set('num_export', lastNumExport)
            // GetAll DocSets
            try { 
              console.log('putting categories ...')
              await Promise.all(docSyncPromises)
              console.log('docsets are in db')
              console.log('get Docsets')
              window.gtvapp.docsets = await this.db.getSQL('DocSets')
              console.log('got Docsets : ' + window.gtvapp.docsets.length)
            } catch (ex) { console.log(JSON.stringify(ex)) }
              
            // console.log(JSON.stringify(window.gtvapp.docsets))
            // window.gtvapp.docsets = await this.db.get('DocSets')
            window.gtvapp.wait.c--
            this.$forceUpdate();
            // Switch to /category vue
            this.$router.push('/category')
          },
          async err => {
            // Disconnected : Get the last data from local DB
            window.gtvapp.wait.c--
            window.gtvapp.messages.push({type:'info', message: `Fonctionnement hors connexion`})
            setInterval(()=>{window.gtvapp.messages.pop()}, 5000)
            try { window.gtvapp.categories = await this.db.getSQL('Categories') } catch (ex) { console.log(JSON.stringify(ex)) }
            // window.gtvapp.categories = await this.db.get('Categories')
            try { window.gtvapp.docsets = await this.db.getSQL('DocSets') } catch (ex) { console.log(JSON.stringify(ex)) }
            // window.gtvapp.docsets = await this.db.get('DocSets')
            this.$router.push('/category')
          }
        )
      } else {
        window.gtvapp.wait.c--
        this.$router.push('/category')
      }
    }
  },
  created () {
    this.checkForUpdate()
    this.getLinks()
  }
}


