export default {
  name: 'Category',
  data () {
    return {
      categories : window.gtvapp.categories,
      currentCategories: [],
      docsets: window.gtvapp.docsets,
      db: window.gtvapp.db,
      current: undefined,
      md: new window.utils.md(),
      search: {
        query: undefined
      },
      links: [],
      page: 0,
      pageSize: 10
    }
  },
  methods: {
    range (s, e) {
      let ret = []
      for (let i = 0; i <= e; i++) {
        ret.push(i)
      }
      return ret
    },
    async rootCat (id) {
      if(this.categories == undefined) {
        try { this.categories = await this.db.getSQL('Categories') } catch (ex) { console.log(JSON.stringify(ex)) }
        // this.categories = await this.db.get('Categories')
      }
      let ret
      if(this.categories !== undefined){
        ret = this.categories.filter(function(cat) {
          return cat.parent == id
        })
      } else {
        ret = []
      }
      return ret
    },
    async getDocSet(id) {
      let res
      try { res = await this.db.getSQL('DocSets', parseInt(id)) } catch (ex) { console.log(JSON.stringify(ex)) }
      return res
      // return await this.db.get('DocSets', parseInt(id))
    },
    async getContent () {
      let root = this.$route.params.id
      try { this.currentCategories = await this.rootCat(root) } catch (ex) { console.log(JSON.stringify(ex)) }
      if(root !== undefined && root !== 'root'){
        try { this.current = await this.db.getSQL('Categories', parseInt(root)) } catch (ex) { console.log(JSON.stringify(ex)) }
        // this.current = await this.db.get('Categories', parseInt(root))
        if(this.current !== undefined && (this.current.child===undefined || this.current.child.length==0) && this.current.docsets.length>0) {
          this.current.popDocsets = []
          this.current.docsets.forEach(async (id) => {
            try {
              console.log(id)
              let d = await this.db.getSQL('DocSets', id)
              this.current.popDocsets.push(d)
            } catch (ex) {
              console.log(JSON.stringify(ex))
            }
            this.$forceUpdate()
            // let d = await this.db.get('DocSets', id)
          })
        }
      } else {
        this.current = undefined
      }
      this.$forceUpdate()
    }
  },
  watch: {
    '$route': async function () {
      this.getContent()
    }
  },
  async mounted () {
    this.getContent()
  }
}


